<template>
  <div class="progress clearfix">
    <span class="current-time">{{dealTime(audioDatas.nowTime)}}</span>
    <div ref="progressBar" class="progress-bar" @click="progressClick">
      <div class="progress-bg">
        <div ref="progressActive" class="progress-active" :style="{width:activeWidth+'px'}">
          <div ref="dots" class="dots" @mousedown="dotsMousedown"></div>
        </div>
      </div>
    </div>
    <span class="end-time">{{dealTime(audioDatas.endTime)}}</span>
  </div>
</template>

<script>
export default {
  props: ["audioDatas"],
  mounted() {
    this.progressBar = this.$refs.progressBar;
    this.progressActive = this.$refs.progressActive;
    this.dots = this.$refs.dots;

    this.bgWidth = this.progressBar.offsetWidth;
    this.dotsWidth = this.dots.offsetWidth;
    this.newBgWidth = this.bgWidth - this.dotsWidth / 2;
  },
  data() {
    return {
      progressBar: null,
      progressActive: null,
      dots: null,
      bgWidth: null,
      dotsWidth: null,
      newBgWidth: null,
      activeWidth: 0
    };
  },
  computed: {
    isPause() {
      return this.audioDatas.isPause;
    }
  },
  watch: {
    isPause(val) {
      // val为true,是暂停状态
      if (val) {
        clearInterval(this.audioDatas.timer);
        this.audioDatas.timer = null;
      } else {
        if (this.audioDatas.timer == null) {
          this.audioDatas.timer = setInterval(this.progressMove, 200);
        }
      }
    }
  },
  methods: {
    progressMove() {
      var currentTime = this.audioDatas.audio.currentTime;
      this.audioDatas.nowTime = currentTime;
      this.activeWidth =
        (currentTime / this.audioDatas.endTime) * this.newBgWidth;
    },
    // 数字格式处理函数如果是1位就在前面补上0
    addZero(str) {
      if (typeof str == "number") {
        str = str.toString();
      }
      return str.replace(/^(\d)$/, "0$1");
    },
    // 时间处理函数
    dealTime(seconds) {
      // seconds为总时间秒数
      var hour = Math.floor(seconds / (60 * 60));
      var min = Math.floor((seconds % (60 * 60)) / 60);
      var sec = Math.floor((seconds % (60 * 60)) % 60);
      if (hour == 0) {
        return this.addZero(min) + ":" + this.addZero(sec);
      } else {
        return (
          this.addZero(hour) + ":" + this.addZero(min) + ":" + this.addZero(sec)
        );
      }
    },
    // dotsMousedown拖拽进度球
    dotsMousedown(e) {
      var _this = this;
      e.stopPropagation();
      clearInterval(this.audioDatas.timer);
      this.audioDatas.timer = null;

      document.onmousemove = function(e) {
        e.stopPropagation();
        var x = e.clientX - _this.progressBar.offsetLeft;
        if (x <= 0) {
          x = 0;
        } else if (x >= _this.newBgWidth) {
          x = _this.newBgWidth;
        }
        _this.activeWidth = x;
        _this.audioDatas.nowTime =
          (x / _this.newBgWidth) * _this.audioDatas.endTime;
      };
      document.onmouseup = function() {
        document.onmousemove = null;
        document.onmouseup = null;
        _this.audioDatas.audio.currentTime = _this.audioDatas.nowTime;
        if (!_this.isPause) {
          if (_this.audioDatas.timer == null) {
            _this.audioDatas.timer = setInterval(_this.progressMove, 200);
          }
        }
        if (_this.audioDatas.audio.ended) {
          console.log("结束");
        }
      };
    },
    // 点击进度条
    progressClick(e) {
      var x = e.clientX - this.progressBar.offsetLeft;
      if (x <= 0) {
        x = 0;
      } else if (x >= this.newBgWidth) {
        x = this.newBgWidth;
      }
      this.activeWidth = x;
      this.audioDatas.nowTime=x/this.newBgWidth*this.audioDatas.endTime;
      this.audioDatas.audio.currentTime=this.audioDatas.nowTime
    }
  }
};
</script>

<style lang="scss" scoped>
.progress {
  display: inline-block;
  height: 30px;
  vertical-align: middle;
}
.progress .current-time {
  float: left;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  color: #fff;
  margin-right: 10px;
  width: 64px;
  text-align: right;
}
.progress .progress-bar {
  float: left;
  position: relative;
  width: 240px;
  height: 10px;
  margin: 10px 0;
  cursor: pointer;
}
.progress .progress-bar .progress-bg {
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 4px;
  background-color: rgba(0, 0, 0, 0.3);
  transition: height 0.3s ease;
}
.progress .progress-bar .progress-bg::before {
  position: absolute;
  display: block;
  content: "";
  background: #fff;
  height: 100%;
}
.progress .progress-bar .progress-bg::before {
  width: 4.5px;
  left: -4.5px;
}

.progress .progress-bar:hover .progress-bg {
  height: 4px;
}
.progress .progress-bar .progress-active {
  position: absolute;
  display: flex;
  align-items: center;
  top: 0;
  left: 0;
  background-color: #fff;
  height: 100%;
  width: 0px;
}
.progress .progress-active .dots {
  position: absolute;
  /* top: -4px; */
  right: -5px;
  width: 10px;
  height: 10px;
  background: #5775f8;
  border-radius: 50%;
  cursor: pointer;
}
.progress .end-time {
  float: left;
  color: #fff;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  margin-left: 10px;
  width: 64px;
  text-align: left;
}
</style>