<template>
  <div class="volume" @mouseenter="volumeBtnMouseenter" @mouseleave="volumeBtnMouseleave">
    <div class="btn-con">
      <i class="volume-btn" :class="volumeClass" @click="volumeBtnClick"></i>
    </div>

    <transition name="up">
      <div class="volume-con" v-show="isShow">
        <div ref="volumeBar" class="volume-bar" @mousedown="volumeMousedown">
          <div class="volume-bg">
            <div ref="volumeActive" :style="{height:volumeHeight+'px'}" class="volume-active">
              <div ref="volumeDots" class="dots" @mousedown="dotsMousedown"></div>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  props: ["audioDatas"],
  mounted() {
    this.volumeBar = this.$refs.volumeBar;
    this.volumeActive = this.$refs.volumeActive;
    this.volumeDots = this.$refs.volumeDots;

    this.volumeBarHeight = this.volumeBar.offsetHeight;
    this.volumetDotsHeight = this.volumeDots.offsetHeight;
    // this.newVolumeBar = this.volumeBarHeight - this.volumetDotsHeight / 2;
    this.volumeHeight = this.newVolumeBar / 2;
    this.volumeMark = this.volumeHeight;
  },
  data() {
    return {
      volumeBar: null,
      volumeActive: null,
      volumeDots: null,

      volumeBrHeight: null,
      volumetDotsHeight: null,
      newVolumeBar: 97,
      volumeHeight: null,
      // volumeMark记录点击静音前的音量
      volumeMark: null,
      // isShow音量条是否显示
      isShow: false,
      // ishover音量条是否在拖拽状态
      ishover: false
    };
  },
  computed: {
    volumeClass: function() {
      return {
        "icon-jingyin": this.audioDatas.isMute,
        "icon-yinliang": !this.audioDatas.isMute
      };
    }
  },
  watch: {
    volumeHeight(val) {
      this.audioDatas.audio.volume = val / this.newVolumeBar;
      if (val == 0) {
        this.audioDatas.isMute = true;
      } else {
        this.audioDatas.isMute = false;
      }
    }
  },
  methods: {
    volumeBtnMouseenter() {
      this.isShow = true;
    },
    volumeBtnMouseleave() {
      if (!this.ishover) {
        this.isShow = false;
      }
    },

    // volumeBtnClick点击音量图标
    volumeBtnClick() {
      this.audioDatas.isMute = !this.audioDatas.isMute;
      if (this.audioDatas.isMute) {
        this.volumeHeight = 0;
      } else {
        if (this.volumeMark == 0) {
          this.volumeMark = this.newVolumeBar / 2;
        }
        this.volumeHeight = this.volumeMark;
      }
    },
    // dotsMousedown拖拽音量球
    dotsMousedown(e) {
      e.stopPropagation();
      var _this = this;
      this.ishover = true;
      document.onmousemove = function(e) {
        e.stopPropagation();
        var y = e.clientY - _this.volumeBar.getBoundingClientRect().y;
        if (y <= 0) {
          y = 0;
        } else if (y >= _this.newVolumeBar) {
          y = _this.newVolumeBar;
        }
        _this.volumeHeight = _this.newVolumeBar - y;
        _this.volumeMark = _this.volumeHeight;

        console.log(_this.volumeBar.getBoundingClientRect().y);
      };

      document.onmouseup = function() {
        document.onmousemove = null;
        document.onmouseup = null;
        _this.ishover = false;
        _this.isShow = false;
      };
    },
    // volumeMousedown点击音量条
    volumeMousedown(e) {
      e.stopPropagation();
      var y = e.clientY - this.volumeBar.getBoundingClientRect().y;
      if (y <= 0) {
        y = 0;
      } else if (y >= this.newVolumeBar) {
        y = this.newVolumeBar;
      }
      this.volumeHeight = this.newVolumeBar - y;
      this.volumeMark = this.volumeHeight;
    }
  }
};
</script>

<style lang="scss" scoped>
.volume {
  display: inline-block;
  position: relative;
  margin-left: 10px;
  vertical-align: middle;
}
.volume .btn-con {
  width: 30px;
  height: 30px;
}
.volume .volume-btn {
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  color: #fff;
  font-size: 30px;
  cursor: pointer;
}
.volume .volume-btn:hover {
  color: #ccc;
}
.volume-con {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  // visibility: hidden;
  left: 0px;
  top: -120px;
  width: 30px;
  height: 120px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 5px;
  overflow: hidden;
}
// .volume:hover .volume-con {
//   visibility: visible;
// }

.volume-bar {
  width: 10px;
  height: 100px;
  cursor: pointer;
}
.volume-bg {
  position: relative;
  width: 2px;
  margin: 0 auto;
  height: 100%;
  background: rgb(124, 122, 122);
}
.volume-bg::before {
  position: absolute;
  display: block;
  content: "";
  background: rgb(245, 48, 48);
  width: 4px;
  height: 3px;
  bottom: -3px;
  left: -1px;
}

.volume-bg .volume-active {
  position: absolute;
  bottom: 0;
  left: -1px;
  width: 4px;
  height: 50px;
  background: rgb(245, 48, 48);
}

.volume-bg .volume-active .dots {
  position: absolute;
  top: -3px;
  margin-left: -6px;
  width: 16px;
  height: 6px;
  border-radius: 2.5px;
  background: #5775f8;
  cursor: pointer;
}
@keyframes up-in{
  from{
    // transform: translateY(100px);
    top: 0;
    height: 0;
  }
  to{
    // transform: translateY(0);
    height: 120px;
      top: -120px;
  }
}
@keyframes down-out{
  from{
    // transform: translateY(100px);
    height: 120px;
      top: -120px;
  }
  to{
    // transform: translateY(0);
    top: 0;
    height: 0;
    
  }
}
.up-enter-active{
  animation: up-in 0.3s;
}
.up-leave-active{
  animation: down-out 0.3s;
}
</style>