<template>
  <div>
    <div class="img-con"
    :style="{animationPlayState :isRun}"
    @click="imgClick">
      <img src="../img/circle.jpg" alt />
    </div>
  </div>
</template>

<script>
export default {
   props: ["audioDatas"],
   data(){
    return{
// running:"paused"
    }
  },
  computed:{
    isRun(){
      return this.audioDatas.isPause?"paused":"running"
    }
  },
  watch:{

  },
  methods:{
    imgClick(){
      this.audioDatas.isPause=!this.audioDatas.isPause
    }
  }
};
</script>

<style lang="scss" scoped>

.img-con {
  width: 120px;
  height: 120px;
  border: 50px solid #444;
  border-radius: 50%;
  margin: 0 auto;
  overflow: hidden;
  cursor: pointer;
  animation: imgRotate 4s infinite linear paused;
}
/* cd图案旋转 */
@keyframes imgRotate{
  0%{
    transform: rotateZ(0);
  }
  100%{
    transform: rotateZ(360deg);
  }
}


.img-con img {
  width: 100%;
  height: 100%;
}
</style>