<template>
  <div class="btn">
    <div class="prev-btn">
      <i class="icon-shangyishou"></i>
    </div>
    <div class="play-btn" @click="play">
      <i :class="pauseClass"></i>
    </div>
    <div class="next-btn">
      <i class="icon-kuaijin"></i>
    </div>
  </div>
</template>

<script>
// import { mapState } from "vuex";

export default {
  props:["audioDatas"],
  data() {
    return {
      // isPlay播放还是暂停
      isPlay: true,
    };
  },
  computed: {
    pauseClass: function() {
      return {
        "icon-zanting": !this.audioDatas.isPause,
        "icon-play_icon": this.audioDatas.isPause
      };
    }
  },
  watch: {},
  methods: {
    // play事件点击播放、暂停按钮
    play() {
      this.audioDatas.isPause=!this.audioDatas.isPause;
    }
  }
};
</script>

<style lang="scss" scoped>
.btn {
  display: inline-block;
  height: 30px;
  vertical-align: middle;
}

.btn > div {
  display: inline-block;
  width: 30px;
  height: 30px;
  color: #fff;
  line-height: 30px;
  text-align: center;
  font-size: 30px;
  cursor: pointer;
  margin-right: 5px;
}
.btn > div:hover {
  color: #ccc;
}
</style>