<template>
  <div>
    <div class="title-con">
      <!-- <span class="title" :style="{animationPlayState :isRun}">青城山下白素贞</span> -->
      <span class="title1" :style="{left :left1+'px'}">青城山下白素贞</span>
      <span class="title2" :style="{left :left2+'px'}">青城山下白素贞</span>
    </div>
  </div>
</template>

<script>
export default {
  props: ["audioDatas"],
   data(){
    return{
      left1:300,
      left2:3600,
      timer:null,
    }
  },
  computed:{
    isRun(){
      return this.audioDatas.isPause?"paused":"running"
    }
  },
  watch:{
    isRun(val){
      if(val=="running"){
        if(this.timer==null){
          this.timer=setInterval(this.move,16.7)
        }
      }else{
        clearInterval(this.timer);
        this.timer=null;
      }
    }
  },
  methods:{
    move(){
      this.left1-=1;
      this.left2-=1;
      if(this.left1==0){
        this.left2=300
      }
      if(this.left2==0){
        this.left1=300
      }
    },
  }
};
</script>

<style lang="scss" scoped>

.title-con{
  position: relative;
  width: 300px;
  height: 30px;
  overflow:hidden;
  margin:0 auto 10px;
  // border: 1px solid red;
}
.title-con span{
  position: absolute;
  color: #fff;
  display: inline-block;
  white-space: nowrap;
  text-align: center;
  height:30px;
  line-height: 30px;
}


@keyframes titleMove{
  0%{
    right: 0;
    transform: translateX(100%);
  }
  100%{
    right: 100%;
    transform: translateX(0);
  }
}
.title{
animation: titleMove 7s infinite linear paused;
}
</style>