<template>
  <div id="app">
    <div class="player">
      <main class="player-main">
        <div class="audio-con">
          <audio
            ref="audio"
            src="./static/audio/鞠婧祎 - 青城山下白素贞.mp3"
            class="audio"
            @canplay="canplay"
            @ended="ended"
          ></audio>
        </div>
        <music-title :audioDatas="audioDatas"></music-title>
        <rotate-CD :audioDatas="audioDatas"></rotate-CD>
      </main>

      <div class="control">
        <div class="control-con">
          <change-btn :audioDatas="audioDatas"></change-btn>

          <progress-bar :audioDatas="audioDatas"></progress-bar>

          <volume-bar :audioDatas="audioDatas"></volume-bar>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 标题组件
import musicTitle from "./components/music_title";
// 旋转CD图案组件
import rotateCD from "./components/rotate_CD";
// 切换歌曲按钮组件
import changeBtn from "./components/change_btn";
// 进度条组件
import progressBar from "./components/progress_bar";
// 音量条组件
import volumeBar from "./components/volume_bar";

export default {
  name: "App",
  components: {
    rotateCD,
    musicTitle,
    changeBtn,
    progressBar,
    volumeBar
  },
  created() {
    document.title = "音乐播放器";
  },
  mounted() {
    this.audioDatas.audio = this.$refs.audio;
  },
  data() {
    return {
      musicList: [
        "./static/audio/黄美珍 - 途中.mp3",
        "./static/audio/鞠婧祎 - 青城山下白素贞.mp3"
      ],
      // audioDatas是audio所要用到的数据
      audioDatas: {
        audio: undefined,
        // isPause是否暂停
        isPause: true,
        // isMute是否静音
        isMute: false,
        // nowTime歌曲现在时间
        nowTime: 0,
        // endTime歌曲结束时间
        endTime: 0,
        // timer计时器
        timer: null
      }
    };
  },
  computed: {
    isPause() {
      return this.audioDatas.isPause;
    }
  },
  watch: {
    isPause(val) {
      if (val) {
        this.audioDatas.audio.pause();
      } else {
        this.audioDatas.audio.play();
      }
    }
  },
  methods: {
    canplay() {
      this.audioDatas.endTime = this.audioDatas.audio.duration;
    },
    ended() {
      this.audioDatas.audio.pause();
      this.audioDatas.isPause = true;
      this.audioDatas.nowTime = 0;
    }
  }
};
</script>




